@import './Variables.scss';


.creator{
    @include maxWidthmaxHeight(100%,auto);
    .creator-title{
        @include maxWidthmaxHeight(100%,100px);
        background: rgb(255, 255, 255);
        @include displayFlex(row);
        @media(max-width:768px){
            @include displayFlex(column);
            @include maxWidthmaxHeight(100%,150px);
        }
        p{
            position: relative;
            @include maxWidthmaxHeight(30%,50%);
            @include top50;
            @include paddingMargin(5px,0);
            text-transform: uppercase;
            font-size: 1.5rem;
            color:rgb(173, 173, 173);
            text-align: center;
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,50%);
                top:0%;
                transform: translateY(0);
                @include paddingMargin(10px,0);
            }
            @media(max-width:300px){
                font-size: 0.9rem;
            }
        }
        .creator-list{
            position: relative;
            @include maxWidthmaxHeight(70%,100%);
            background: rgb(63, 7, 168);
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,50%);
            }
            ul{
                @include maxWidthmaxHeight(100%,auto);
                list-style-type: none;
                @include displayFlex(row);
                @include top50;
                justify-content: center;
                li{
                    color: white;
                    margin-left:10px;
                    margin-right:10px;
                    cursor: pointer;
                    &:hover{
                        color:red;
                    }
                    @media(max-width:768px){
                        font-size: 0.8rem;
                    }
                    @media(max-width:300px){
                        font-size: 0.7rem;
                    }
                }
            }
        }
    }
    .creator-content{
        @include maxWidthmaxHeight(100%,auto);
        @include paddingMargin(0,10px auto);
        @include displayFlex(row);
        @media(max-width: 768px){
            @include displayFlex(column);
        }
        .creator-pattern{
            @include maxWidthmaxHeight(25%,auto);
            box-shadow: 0 0 5px rgb(61, 1, 1);
            @include paddingMargin(0,0);
            @media(max-width: 768px){
                @include maxWidthmaxHeight(100%,auto); 
            }
            .creator-pattern-title{
                @include maxWidthmaxHeight(100%,80px);
                background: rgb(67, 5, 117);
                p{
                    text-align: center;
                    color:rgb(223, 223, 223);
                    @include top50;
                    font-size: 1.1rem;
                    @media(max-width: 768px){
                        font-size: 1rem;
                    }
                }
            }
            .creator-pattern-content{
                @include maxWidthmaxHeight(100%,auto);
                @include paddingMargin(0,0);
                
                .color-pattern{
                    @include maxWidthmaxHeight(100%,150px);
                    @include displayFlex(row);
                    justify-content: center;
                    .color-pattern-element{
                        position: relative;
                        top:30%;
                        @include maxWidthmaxHeight(40px,40px); 
                        margin: 5px;
                        cursor: pointer;
                        transform: skew(-20deg);
                        box-shadow: 0 0 5px rgb(124, 124, 124);
                        transition: all 0.2s;
                    }
                }
                .creator-pattern-sizes{
                    @include maxWidthmaxHeight(100%,180px);
                    @include paddingMargin(0, 0);
                    background-color: rgb(223, 223, 223);
                    p{
                        @include maxWidthmaxHeight(100%,50px); 
                        font-size: 1.1rem;
                        padding-top: 10px;
                        text-align: center;
                        color:rgb(68, 68, 68);
                        @media(max-width: 768px){
                            font-size: 1rem;
                        }
                    }
                    ul{
                        @include maxWidthmaxHeight(100%,auto);
                        list-style-type: none;  
                        @include displayFlex(row);
                        padding:0;
                        justify-content: center;
                        li{
                            width:60px;
                            height:50px;
                            box-shadow: 0 0 5px rgb(156, 156, 156);
                            border-bottom: 2px solid rgb(104, 0, 0);
                            @media(max-width: 992px){
                                @include maxWidthmaxHeight(50px,50px);  
                            }
                            p{  
                                background-color: white;
                                @include maxWidthmaxHeight(100%,100%);
                                color:rgb(53, 53, 53);
                                padding: 10px;
                                font-size: 1.2rem;
                                @media(max-width: 992px){
                                    font-size: 1.1rem;  
                                }
                            }
                        }
                    }
                }
                .theme-pattern-button{
                    @include maxWidthmaxHeight(100%,auto);
                    background-color: rgb(255, 255, 255);
                    button{
                        @include maxWidthmaxHeight(130px,60px);
                        @include paddingMargin(0,10px auto);
                        background: rgb(253, 253, 253);
                        box-shadow: 0 0 5px rgb(85, 85, 85);
                        color:rgb(60, 10, 124);
                        border: none;
                        outline: none;
                        text-transform: uppercase;
                        display: none;
                        @media(max-width: 768px){
                            display:block;
                            @include maxWidthmaxHeight(130px,60px);
                            font-size: 0.9rem;
                        }
                    }
                }
                .theme-pattern::-webkit-scrollbar{
                    width: 10px;
                }
                .theme-pattern::-webkit-scrollbar-thumb{
                    background: rgb(201, 201, 201);
                    border-radius: 20px;
                }
                .theme-pattern{
                    position: relative;
                    @include maxWidthmaxHeight(100%,500px);
                    overflow:scroll;
                    @media(max-width:992px){
                        @include maxWidthmaxHeight(100%,350px);
                    }
                    @media(max-width:768px){
                       @include maxWidthmaxHeight(100%,180px);
                       overflow-y: visible;
                       white-space: nowrap;
                    }
                    .theme-pattern-element{
                        @include maxWidthmaxHeight(80%,200px);
                        @include paddingMargin(0,5px auto);
                        color:white;
                        text-align: center;
                        cursor: pointer;
                        box-shadow: 0 0 5px silver;
                        img{
                            @include top50;
                            width: 10rem;
                        }
                        @media(max-width: 992px){
                            @include maxWidthmaxHeight(100%,150px); 
                        }
                        @media(max-width: 768px){
                            display: inline-block;
                            @include top50;
                            @include maxWidthmaxHeight(50%,150px); 
                            @include paddingMargin(0,5px 10px)
                        }
                    }
                }
            }
        }
        .creator-draw{
            position: relative;
            @include maxWidthmaxHeight(50%,auto);
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,auto);
                background: #000;
                 @include paddingMargin(5px,0);
            }
            .tshirt{
                position: relative;
                @include maxWidthmaxHeight(600px,700px);
                margin-top:5%;
                transform: translateY(-5%);
                left:50%;
                transform: translateX(-50%);
                background: url('../img/tshirt.png');
                background-size: cover;
                background-position: center;
                box-shadow: 0 0 10px rgb(122, 122, 122);
                @media(max-width:1200px){
                    @include maxWidthmaxHeight(600px,700px);
                }
                @media(max-width:992px){
                    @include maxWidthmaxHeight(500px,600px);
                }
                @media(max-width:800px){
                    @include maxWidthmaxHeight(450,550px);
                    position: relative;
                    margin-top:2%;
                    transform: translateY(-2%);
                    left:50%;
                    transform: translateX(-50%);
                }
                @media(max-width:576px){
                    @include maxWidthmaxHeight(90%,600px);
                }
                @media(max-width:450px){
                    @include maxWidthmaxHeight(100%,500px);
                }
                @media(max-width:350px){
                    @include maxWidthmaxHeight(95%,400px);
                }
                @media(max-width:300px){
                    @include maxWidthmaxHeight(100%,350px);
                }
                .tshirt-content{
                    position: absolute;
                    width: 40%;
                    height: 60%;
                    margin-top: 20%;
                    margin-left:50%;
                    transform: translateX(-50%);
                    @media(max-width:576px){
                        width: 42%;
                    }
                    @media(max-width:350px){
                        width: 45%;
                    }
                    .tshirt-content-element{
                        position: relative;
                        @include maxWidthmaxHeight(100%,auto);
                        text-align: center;
                        cursor: move;

                        img{
                            @include maxWidthmaxHeight(100%,5%);
                        }
                    }
                }
            }
        }
        .creator-settings{
            @include maxWidthmaxHeight(30%,auto);
            box-shadow: 0 0 5px silver;
            p{
                width: 100%;
                text-align: center;
                padding:20px;
                background: rgb(223, 223, 223);
            }
            .creator-sett-content{
                @include maxWidthmaxHeight(100%,100px);
                @include displayFlex(row);
                justify-content: center;
                flex-wrap: wrap;
                border-bottom: 1px solid black;
                button{
                    @include maxWidthmaxHeight(80px,80px);
                    @include paddingMargin(0,5px);
                    @include top50;
                    background: rgb(255, 255, 255);
                    box-shadow: 0 0 5px silver;
                    border:none;
                    font-size: 0.8rem;
                    .fas{
                        display: block;
                        @include paddingMargin(0,5px auto);
                        font-size: 1.5rem;
                    }
                }
            }
            .creator-summary{
                @include maxWidthmaxHeight(100%,auto);
                .summary-title{
                    text-align: center;
                    @include paddingMargin(20px,0);
                }
                .summary-element{
                    @include maxWidthmaxHeight(100%,80px);
                    box-shadow: 0 0 5px rgb(197, 190, 190);
                    @include displayFlex(row);
                    p{
                        @include maxWidthmaxHeight(auto,auto);
                        @include top50;
                        background-color: white;
                        text-align: left;
                    }
                    input{
                        @include maxWidthmaxHeight(50px,50px);
                        @include top50;
                        border: none;
                        box-shadow: 0 0 5px silver;
                    }
                    .price{
                        @include top50;
                        background-color: white;
                        font-size: 1.2rem;
                        color:rgb(60, 10, 117);
                    }
                }
                .buy{
                    position: relative;
                    @include maxWidthmaxHeight(100px,50px);
                    @include paddingMargin(0,0 50%);
                    transform: translate(-50%);
                    border:none;
                    outline: none;
                    box-shadow: 0 0 5px silver;
                    background-color: rgb(149, 247, 149);
                }
            }
        }
    }
}